import "./App.css"


const embedConfig = {
    "link_color": "fafafa",
    "border_width": 2,
    "bg_color": "000000",
    "fg_color": "c7c7c7",
    "border_color": "ffffff"
}

const codeToSrc = code => {

    const itchioHeader = "https://itch.io/embed/";

    const parameters = Object
        .keys(embedConfig)
        .map(k=>`${k}=${embedConfig[k]}`)
        .join("&")

    return `${itchioHeader}${code}?${parameters}`;
};

export function GameCard({data: {code, tittle, url}}) {


    return <iframe frameBorder={0} src={codeToSrc(code)} className={"game"}>
        <a href={url}>{tittle}</a>
    </iframe>

}